const API_KEY = "75f6ac300ef95ee23a13b015f0ea6906";
const BASE_FETCH = "https://api.themoviedb.org/3";

interface IMovie {
    id: number;
    backdrop_path: string;
    poster_path: string;
    title: string;
    overview: string;
}

export interface IGetMoviesResult {
    dates: {
        minimum: string;
        maximum: string;
    };
    page : number;
    results: IMovie[];
    total_pages: number;
    total_results: number;
}


export function getMovies() {
    return fetch(`${BASE_FETCH}/movie/now_playing?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
};